import React, { useState } from "react";

const UploadReplay: React.FC = () => {
  const [division, setDivision] = useState(1);
  const [week, setWeek] = useState(1);
  const [team1, setTeam1] = useState("");
  const [team2, setTeam2] = useState("");
  const [file, setFile] = useState<File | null>(null);

  const handleUpload = async () => {
    if (!file) return alert("Please select a replay file.");
    if (!team1 || !team2) return alert("Please enter both teams.");

    const filename = `${team1}vs${team2}.html`;
    const path = `d${division}/w${week}/${filename}`;

    const formData = new FormData();
    formData.append("file", file);
    formData.append("path", path);

    // FIRST attempt: check if exists
    let res = await fetch("/api/uploadReplay", {
      method: "POST",
      body: formData
    });

    const json = await res.json();

    // If file exists â†’ ask user
    if (json.exists) {
      const ok = confirm(
        `A replay already exists at:\n${path}\n\nOverwrite it?`
      );
      if (!ok) return;

      // Second request WITH overwrite flag
      const formData2 = new FormData();
      formData2.append("file", file);
      formData2.append("path", path);
      formData2.append("overwrite", "true");

      res = await fetch("/api/uploadReplay", {
        method: "POST",
        body: formData2
      });

      if (!res.ok) return alert("Failed to overwrite replay.");

      return alert("Replay overwritten successfully!");
    }

    if (json.success) {
      alert("Replay uploaded successfully!");
    }
  };


  return (
    <div style={{ padding: 20, maxWidth: 400 }}>
      <h2>Upload Replay</h2>

      <label>
        Division:
        <select value={division} onChange={(e) => setDivision(Number(e.target.value))}>
          {[1, 2, 3].map((d) => (
            <option key={d} value={d}>{d}</option>
          ))}
        </select>
      </label>

      <br /><br />

      <label>
        Week:
        <select value={week} onChange={(e) => setWeek(Number(e.target.value))}>
          {[1,2,3,4,5,6,7,8,9].map((w) => (
            <option key={w} value={w}>{w}</option>
          ))}
        </select>
      </label>

      <br /><br />

      <input
        type="text"
        placeholder="Team 1"
        value={team1}
        onChange={(e) => setTeam1(e.target.value)}
      />

      <br /><br />

      <input
        type="text"
        placeholder="Team 2"
        value={team2}
        onChange={(e) => setTeam2(e.target.value)}
      />

      <br /><br />

      <input
        type="file"
        accept=".html"
        onChange={(e) => setFile(e.target.files?.[0] || null)}
      />

      <br /><br />

      <button onClick={handleUpload}>Upload Replay</button>
    </div>
  );
};

export default UploadReplay;